	var strShareUrl = ""
	var strShareTitle = ""
	var strShareSummary = ""
	var strShareImageUrl = ""











































































































































































































































































































































export default {
	props:['datas'],
	data() {
		return {
			title: ''
		}
	},
	onLoad() {},
	onBackPress() {
		//监听back键，关闭弹出菜单
		if (nvImageMenu.isVisible()) {
			nvImageMenu.hide()
			nvMask.hide()
			return true
		}
	},
	onNavigationBarButtonTap(e) {
		switch (e.type) {
			case "share": //点击分享按钮
				//TODO 未处理h5，h5应调用qq浏览器等自带的share api，如果浏览器不自带share，那么分享图标不应该显示。或者从简的话，h5整个就不显示分享按钮




				break;
		}
	},
	methods: {
		share(){
			let data = this.datas
			strShareUrl = data.Url
			strShareTitle = data.Title
			strShareSummary = data.Summary
			strShareImageUrl = data.ImageUrl
			nvMask.show()
			nvImageMenu.show() //5+应支持从底部向上弹出的动画
		}
	}
}
